/* General Reset */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Roboto", sans-serif;
}

/* Body Styling */
body {
  background: #f7f9fc; /* Light, neutral background */
  color: #2d3436; /* Dark text for contrast */
  font-size: 16px;
  line-height: 1.6;
}

/* Header Styling */
header {
  background: linear-gradient(135deg, #6a11cb, #2575fc);
  color: white;
  text-align: center;
  padding: 1.5rem;
  font-size: 2rem;
  font-weight: bold;
  border-bottom: 3px solid #4a69bd;
  text-shadow: 1px 1px 3px rgba(0, 0, 0, 0.3);
}

/* Main Content */
.content-container {
  display: flex;
  gap: 2rem;
  margin: 2rem;
}

/* Left Panel - Theory Section */
.theory-container {
  flex: 1;
  padding: 1.5rem;
  background: white;
  border-radius: 12px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  border-left: 5px solid #6a11cb;
}

.theory-container h2 {
  font-size: 1.8rem;
  margin-bottom: 1rem;
  color: #2c3e50;
}

.theory-container p {
  font-size: 1.1rem;
  line-height: 1.8;
  color: #555;
  text-align: justify;
}

/* Right Panel - Code Section */
.code-container {
  flex: 1.5;
  background: #f4f7fc;
  border-radius: 12px;
  padding: 1.5rem;
  border: 1px solid #ddd;
  font-family: "Courier New", Courier, monospace;
}

/* Code Textarea */
textarea {
  width: 722px;
  height: 396px;
  font-family: "Courier New", Courier, monospace;
  font-size: 1rem;
  padding: 1rem;
  border: 1px solid #ddd;
  border-radius: 8px;
  background: #f9f9f9;
  margin-bottom: 1rem;
  box-sizing: border-box;
}

/* Run Code Button */
button {
  padding: 0.8rem 2rem;
  background: linear-gradient(135deg, #00b894, #00cec9);
  color: white;
  border: none;
  border-radius: 8px;
  font-size: 1.2rem;
  font-weight: bold;
  cursor: pointer;
  transition: background 0.3s ease, transform 0.2s;
  text-align: center;
}

button:hover {
  background: linear-gradient(135deg, #00cec9, #00b894);
  transform: scale(1.05);
}

/* Output Section */
.output-space {
  margin-top: 1rem;
  padding: 1rem;
  background: #dff9fb;
  border: 1px solid #00cec9;
  border-radius: 8px;
  color: #2d3436;
  font-family: "Courier New", Courier, monospace;
}

.output-space h3 {
  color: #00b894;
  font-size: 1.3rem;
  margin-bottom: 0.5rem;
}

/* Language and Topic Selectors */
.language-selector,
.topic-selector {
  margin-bottom: 1.5rem;
  display: flex;
  flex-direction: column;
}

.language-selector label,
.topic-selector label {
  font-size: 1.2rem;
  margin-bottom: 0.5rem;
}

select {
  padding: 0.8rem;
  border-radius: 8px;
  border: 1px solid #6a11cb;
  font-size: 1rem;
  background-color: #f9f9f9;
}

select:focus {
  outline: none;
  border-color: #2575fc;
}

/* Footer */
footer {
  margin-top: 2rem;
  text-align: center;
  padding: 1rem;
  font-size: 0.9rem;
  color: #7f8c8d;
}
